@use "../util" as *;
// https://codepen.io/SaraSoueidan/pen/jpBbrq

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;

  label {
    font-size: rem(14);
    font-weight: 700;
    color: var(--text-color2);

    &[for="dark"] {
      line-height: rem(24);
      margin-right: rem(13);
    }
  }

  &__wrapper {
    position: relative;
    height: rem(24);
  }

  input[type="radio"] {
    margin: 0 rem(-2) 0 rem(-2);
    opacity: 0;
    width: rem(25);
    height: rem(25);

    &:focus ~ .toggle__switcher {
      border: rem(1) solid var(--text-color1);
    }
  }

  &__background {
    display: block;
    height: 100%;
    position: absolute;
    width: 100%;
    top: 0;
    border-radius: rem(12);
    background: var(--toggle-bg);
    pointer-events: none;
  }

  &__switcher {
    position: absolute;
    left: rem(3);
    top: rem(3);
    right: 100%;
    width: rem(18);
    height: rem(18);
    border-radius: 50%;
    background-color: var(--dark-bg);
    transition: all 150ms ease-in-out;
  }

  #light:checked ~ .toggle__switcher {
    left: calc(100% - 21px);
    right: 3px;
  }

  #system:checked ~ .toggle__switcher {
    left: 50%;
    right: auto;
    transform: translate(-50%);
  }
}
